
import { Options, Vue } from "vue-class-component";
import { store, Movie } from "@/store";
import MovieCard from "./MovieCard.vue";

// eslint-disable-next-line @typescript-eslint/no-explicit-any

@Options({
  components: {
    MovieCard,
  },
  props: {},
})
export default class MainPanel extends Vue {
  store = store;
  phrase = "";

  showAdd = false;
  addOpen = false;
  addName = "";
  addYear = 2000;

  rateOpen = false;
  rated: Movie | null = null;
  score = 5;
  cat1 = 5;
  cat2 = 5;
  cat3 = 5;
  cat4 = 5;
  cat5 = 5;
  cat6 = 5;

  async mounted(): Promise<void> {
    try {
      let base = this.store.state.apiUrl;
      let token = this.store.state.token;
      const url = new URL("/recommended", base);
      let recommended = await fetch(url.toString(), {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (!recommended.ok) {
        throw new Error(await recommended.text());
      }

      this.store.setRecommended(await recommended.json());
    } catch (e) {
      console.log(e);
    }
  }

  async search(): Promise<void> {
    try {
      this.showAdd = false;
      let base = this.store.state.apiUrl;
      const url = new URL("/search", base);
      url.searchParams.append("searchPhrase", this.phrase);

      let results = await fetch(url.toString());
      let res = await results.json();
      this.store.setSearchResults(res);
      if (Array.isArray(res) && res.length === 0) {
        this.showAdd = true;
      }
    } catch (e) {
      console.log(e);
    }
  }

  async rate(): Promise<void> {
    try {
      let base = this.store.state.apiUrl;
      let token = this.store.state.token;
      const data = {
        movieId: this.rated?.id,
        score: this.score / 10,
        cat1: this.cat1,
        cat2: this.cat2,
        cat3: this.cat3,
        cat4: this.cat4,
        cat5: this.cat5,
        cat6: this.cat6,
      };
      const url = new URL("/rate", base);

      await fetch(url.toString(), {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(data),
      });
      this.closeRateModal();
    } catch (e) {
      console.log(e);
    }
  }

  async add(): Promise<void> {
    try {
      let base = this.store.state.apiUrl;
      const data = {
        name: this.addName,
        year: this.addYear,
      };
      const url = new URL("/add", base);

      await fetch(url.toString(), {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(data),
      });
      this.closeAddModal();
    } catch (e) {
      console.log(e);
    }
  }

  showRateModal(rated: Movie): void {
    this.rated = rated;
    this.rateOpen = true;
  }

  closeRateModal(): void {
    this.rated = null;
    this.score = 0;
    this.cat1 = 0;
    this.cat2 = 0;
    this.cat3 = 0;
    this.cat4 = 0;
    this.cat5 = 0;
    this.cat6 = 0;
    this.rateOpen = false;
  }

  showAddModal(): void {
    this.addOpen = true;
  }

  closeAddModal(): void {
    this.addName = "";
    this.addYear = 2000;
    this.addOpen = false;
  }
}
